<template>
	<body>
		<div class="box">
			<div class="bg">
				<h4><span>404</span> 页面不见了捏: <em>您来到了偶像练习生大舞台捏~</em></h4>
				<div class="bar1"></div>
				<div class="bar2"></div>
				<img class="logo" src="@/assets/img/common/logo-kunkun.jpg" />
				<!--图片可替换 -->
				<div @click="handle_pause_music" class="kunkun">
					<div class="kunkun-head"></div>
					<div class="kunkun-body">
						<div class="braces"></div>
						<div class="braces"></div>
						<div class="brace-3"></div>
					</div>
					<div class="kunkun-leg">
						<div class="leg"></div>
						<div class="leg"></div>
					</div>
				</div>
			</div>
			<div class="floor"></div>
			<div class="left">
				<el-tag size="mini" type="success">点击 篮球 回到主页</el-tag>
				<img @click="to_login" class="basketball" src="@/assets/img/common/basketball.jpeg" alt="">
			</div>
			<div class="right">
				<img @click="this.$router.go(-1)" src="@/assets/img/common/Q-kunkun.jpeg" alt="">
				<el-tag size="mini" type="success">点击 蔡鸡 回到上一页</el-tag>
			</div>
		</div>
		<!-- 音乐 -->
		<audio ref="kunkun_mp3" src="@/assets/audio/kunkun-jinitaimei.mp3"></audio>
	</body>
</template>

<script>
export default {
	data() {
		return {

		}
	},
	methods: {
		// 跳转到主页
		to_login() {
			this.$router.push('/login')
		},
		// 点击坤坤暂停/播放音乐
		handle_pause_music() {
			if (!this.$refs.kunkun_mp3.paused) {
				this.$refs.kunkun_mp3.pause()
			} else {
				this.$refs.kunkun_mp3.play()
			}
		},
	},
	mounted() {
		// 弹窗小提示
		this.$notify({
			title: '音乐小提示',
			message: '点击 坤坤 可控制音乐捏',
			duration: 10000,
			type: 'success'
		});
	},
}
</script>

<style lang="less" scoped>
body {
	margin: 0;
}


.box {
	width: 100%;
	height: 100%;
	border: 1px solid #ff00ff;
	position: relative;

	.left,
	.right {
		position: absolute;
		display: inline-block;
		bottom: 100px;

		img {
			width: 80px;
			height: 80px;
			border-radius: 50%;
			vertical-align: middle;
			cursor: pointer;
		}

		img.basketball {
			animation: up_down 1s infinite;
			transition: all 1s;
			transform: translateY(80px);
		}
	}

	.left {
		left: 25%;
	}

	.right {
		right: 25%;
	}
}

.bg {
	position: relative;
	height: 600px;
	background-color: #e4e4f6;
	text-align: center;

	h4 {
		color: #7a7878;
		padding-top: 50px;
		font-weight: 200;

		span {
			font-size: 60px;
			font-weight: normal;
		}
	}
}

.floor {
	height: 200px;
	background-color: #cec3c1;
}

.bar1 {
	position: absolute;
	width: 374px;
	top: 24px;
	left: -154px;
	border-bottom: 110px solid #7f83db;
	border-right: 110px solid transparent;
	transform: rotate(45deg);
}

.bar2 {
	position: absolute;
	width: 673px;
	top: 295px;
	left: -187px;
	border-top: 110px solid #93cae4;
	border-right: 110px solid transparent;
	transform: rotate(45deg);
}

.logo {
	position: absolute;
	top: 32px;
	right: 100px;
	width: 100px;
	height: 200px;
	max-width: 100%;
}

.kunkun {
	position: absolute;
	top: 200px;
	left: 50%;
	margin-left: -150px;
	width: 300px;
	height: 500px;
	border-radius: 10px;
	transform: skew(-20deg, 0) scale(1.1, 0.9);
	animation: TieShanKao 1s infinite;

	.kunkun-head {
		width: 30%;
		height: 20%;
		background-color: rgba(171, 168, 168, 0.615);
		margin-left: 50%;
		border-radius: 50%;
	}

	.kunkun-body {
		width: 100%;
		height: 50%;
		background-color: black;
		display: flex;
		justify-content: space-around;
		border-top-right-radius: 20%;
		border-top-left-radius: 20%;
		position: relative;
		overflow: hidden;

		.braces {
			width: 10%;
			background-color: white;
		}

		.braces:first-child {
			height: 90%;
			transform: rotate(-30deg);
			margin-left: 50px;
			margin-top: -35px;
		}

		.braces:nth-child(2) {
			transform: rotate(45deg);
			height: 85%;
		}

		.brace-3 {
			width: 10%;
			height: 35%;
			background-color: white;
			position: absolute;
			bottom: 0;
			left: 50%;
			margin-left: 4px;
		}
	}

	.kunkun-leg {
		width: 100%;
		height: 30%;
		display: flex;
		justify-content: space-around;

		.leg {
			width: 20%;
			height: 100%;
			background-color: rgb(42, 41, 41);
		}
	}
}

/* 坤坤动画 */
@keyframes TieShanKao {
	0% {
		transform: skew(-5deg, 0) scale(1, 1);
	}

	25% {
		transform: skew(-20deg, -5deg) scale(1, 1.1);
	}

	50% {
		transform: skew(-5deg, 0) scale(1, 1);
	}

	75% {
		transform: skew(-20deg, 0) scale(1.1, 0.9);
	}

	100% {
		transform: skew(-5deg, 0) scale(1, 1);
	}
}

// 篮球动画
@keyframes up_down {
	0% {
		transform: translateY(0);
	}

	15% {
		transform: translateY(20px);
	}

	25% {
		transform: translateY(30px);
	}

	35% {
		transform: translateY(40px);
	}

	45% {
		transform: translateY(50px);
	}

	55% {
		transform: translateY(40px);
	}

	65% {
		transform: translateY(30px);
	}

	75% {
		transform: translateY(20px);
	}

	85% {
		transform: translateY(10px);
	}

	100% {
		transform: translateY(0);
	}
}
</style> 
